en los individuos y las familias que propicien estilos de vida saludables.<br>Educativo:Promueven el ejercicio de la ciudadanía, actuando frente al reconocimiento y exigibilidad de los derechos y la participación social activa.Gestionan la coordinación intersectorial en el marco de políticas públicas e institucionales de los entornos educativos.Facilitan respuestas por medio de la articulación de la oferta de los servicios sociales.Contribuyen al fortalecimiento de habilidades, valores, aptitudes y actitudes.<br>Laboral:Contribuyen al mejoramiento de las condiciones de los trabajadores en su entorno laboral.Promueven condiciones de trabajo saludables para protege la salud y el bienestar de la población trabajadora, sus familias y comunidades.Promueven la participación de los trabajadores en los escenariosImplementan y evaluación de planes y programas para el logro del bienestar colectivo en relación con la salud en el trabajo.Cuentan con información y evidencia acerca situaciones de riesgo, relacionadas con las prácticas que se desarrollan para la toma decisiones.<br>Institucional:Promueven acceso a bienes y servicios para que aporten e incidan en el mejoramiento de la calidad de vida de las comunidades.Promueven la participación de los individuos y comunidades organizadas en los espacios de toma de decisiones para incidir en las decisiones de la gestión pública.Promueven en grupos o colectivos, prácticas de protección y recuperación de entornos y ecosistemas estratégico.<br>Comunitario:Favorecen la inclusión, el respeto y reconocimiento de la diversidad, situaciones sociales como desplazamiento, víctimas del conflicto, habitante de calle, entre otros.Promueven y articulan estrategias como hospitales seguros, hospitales verdes y saludables, entre otras.Desarrollan capacidades a redes sociales y comunitarias, para el abordaje de situaciones relacionadas con el bienestar y la calidad de vida de sus comunidades."}}),_c('figcaption',[_vm._v("Nota. "),_c('a',{staticStyle